import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';
import Button from '../Button/Button';
import './NotFound.css';

function NotFound({ onHeader, stateHeader }) {
  const history = useHistory();
  useEffect(() => {
    if (stateHeader) {
      onHeader(false);
    }
  }, [onHeader, stateHeader]);
  return (
    <div className='NotFound'>
    <h2 className='NotFound__title'>404</h2>
    <p className='NotFound__subtitle'>Ð¡Ñ‚Ñ€Ð°Ð½Ð¸Ñ†Ð° Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½Ð°</p>
    <Button className={'Button__goBack'} type={'button'} title={'ÐÐ°Ð·Ð°Ð´'} onChange={history.goBack}>
      ÐÐ°Ð·Ð°Ð´
    </Button>
    </div>
  );
}

NotFound.propTypes = {
  onHeader: PropTypes.func.isRequired,
  stateHeader: PropTypes.bool.isRequired,
};

export default NotFound;
